.dot {
  position: absolute;
  left: calc(50% - 10px);
  top: calc(50% - 10px);
  background-color: red;
  width: 20px;
  height: 20px;
  border-radius: 50px;
}

#mover {
  -webkit-animation: pedal1 0.5s linear infinite;
          animation: pedal1 0.5s linear infinite;
}
.none {
  display: none;
}
.circle {
  position: absolute;
  left: calc(50% - 75px);
  top: calc(50% - 75px);
  width: 150px;
  height: 150px;
  border-radius: 150px;
  border: 1px solid red;
}

#left {
  left: calc(50% - 75px - 50px);
}

#right {
  left: calc(50% - 75px + 50px);
}

/* arccos(50/75) = 48deg */
@-webkit-keyframes pedal1 {
  0% {
    -webkit-transform: translateX(50px) rotate(48deg) translateX(-75px);
            transform: translateX(50px) rotate(48deg) translateX(-75px);
  }
  50% {
    -webkit-transform: translateX(50px) rotate(-48deg) translateX(-75px);
            transform: translateX(50px) rotate(-48deg) translateX(-75px);
  }
  51% {
    -webkit-transform: translateX(-50px) rotate(48deg) translateX(75px);
            transform: translateX(-50px) rotate(48deg) translateX(75px);
  }
  100% {
    -webkit-transform: translateX(-50px) rotate(-48deg) translateX(75px);
            transform: translateX(-50px) rotate(-48deg) translateX(75px);
  }
}
@keyframes pedal1 {
  0% {
    -webkit-transform: translateX(50px) rotate(48deg) translateX(-75px);
            transform: translateX(50px) rotate(48deg) translateX(-75px);
  }
  50% {
    -webkit-transform: translateX(50px) rotate(-48deg) translateX(-75px);
            transform: translateX(50px) rotate(-48deg) translateX(-75px);
  }
  51% {
    -webkit-transform: translateX(-50px) rotate(48deg) translateX(75px);
            transform: translateX(-50px) rotate(48deg) translateX(75px);
  }
  100% {
    -webkit-transform: translateX(-50px) rotate(-48deg) translateX(75px);
            transform: translateX(-50px) rotate(-48deg) translateX(75px);
  }
}

